import styled from 'styled-components'
import { Link,Navigate, useNavigate } from 'react-router-dom'
import { useState,useEffect, useContext } from 'react'
import axios from 'axios'
import MovieContext  from '../showData'

const Recommends = (props) => {
 //  const [movies] =useContext(MovieContext)
 const  nav = useNavigate()
  const [movies,setMovies] = useState(null);
  const imgURL ="https://image.tmdb.org/t/p/original/";
 // const {trending,url} = useContext(MovieContext)
  console.log("value",props.data);
  useEffect(() =>{
    const fetchData = async () =>{
     await axios.get(`https://api.themoviedb.org/3/movie/now_playing?api_key=d03799692be1c26faf0ade18a4205f9f&language=en-US&page=1`).then((resp) =>{
        console.log("resp recommend",resp.data.results);
       
          setMovies(resp.data.results);
           })
    }
    fetchData()
    .catch(console.error)
  
  },[])
   
    //setMovies(resp.data.data.results);
  return (
 
    <Container>
    <h4>Recommends</h4>
{  !movies ? <div>Data not loaded </div>
    :
<>
  <Content>
     
  {
    movies.map((obj) =>
    <Wrap>
     <img  data ={obj} onClick={() =>{
               nav("/detail/recommends/" + obj.id)
             }
              } src= {`${imgURL + obj.backdrop_path}`} alt=""/>
    </Wrap>  
 )}
 </Content>
 </>
}
 </Container>
          
  )
   }



const Container = styled.div`
padding: 0 0 26px;
`
const Content = styled.div`
display: grid;

grid-column-gap: 25px;
padding: 0px;

grid-template-columns: repeat(auto-fill,minmax(300px,1fr));
grid-auto-flow: column;
grid-auto-columns: minmax(300px,1fr);
overflow-x: auto;

 @media (max-width: 768px) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
`
const Wrap = styled.div`
  padding-top: 56.25%;
  border-radius: 10px;
  box-shadow: rgb(0 0 0 / 69%) 0px 26px 30px -10px,
    rgb(0 0 0 / 73%) 0px 16px 10px -10px;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
  border: 3px solid rgba(249, 249, 249, 0.1);
  img {
    inset: 0px;
    display: block;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    position: absolute;
    transition: opacity 500ms ease-in-out 0s;
    width: 100%;
    z-index: 1;
    top: 0;
  }
  &:hover {
    box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
      rgb(0 0 0 / 72%) 0px 30px 22px -10px;
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
  }
`;

export default Recommends